import React, {Fragment, Component} from 'react'
import {Col, Row} from 'react-bootstrap'
import Redux from './redux-logo.png'

class Page9 extends Component {
    render(){
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Côté client</h2>
                        <ul class="left">
                            <li>Chargement initial plus long que Server-Side Rendering</li>
                            <li>Demandant en RAM</li>
                            <li>Inutile pour les pages statiques</li>
                            <li>Difficile pour le SEO (web crawlers)</li>
                        </ul>
                    </Col>
                    <Col>
                        <h2>Seulement une librairie graphique</h2>
                        <ul class="left">
                            <li>Ne remplace pas les gros frameworks (Angular)</li>
                            <li>Besoin d'autres librairies pour plus de fonctionnalités</li>
                            <li>Généralement utilisé avec Redux pour gérer les états</li>
                        </ul>
                        <img src={Redux} className="image shadowed"/>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page9