import React, {Fragment, Component} from 'react'
import {Button} from 'react-bootstrap'

class TodoList extends Component {
    constructor(props){
        super(props);
        this.state = {value:'', tasks:this.props.data}
        console.log(this.state.tasks)
    }

    handleChange = (event) => {
        this.setState({value:event.target.value});
    }

    addTask = (event) => {
        const value = this.state.value;
        const id = this.state.currentId;
        this.setState({value:'', tasks:[...this.state.tasks, {value:value, id:id}]})
        event.preventDefault();
    }

    removeTask = (id) => {
        const tasks = this.state.tasks
        const filteredTasks = tasks.filter((task) => {return task.id !== id})
        this.setState({tasks:filteredTasks})
    }

    render() {
        return (
        <Fragment>
            <form>
                <label>
                    {"Tâche : "}
                    <input type="text" value={this.state.value} name="task" onChange={this.handleChange}/>
                </label>
                <input type="submit" value="Ajouter" onClick={this.addTask}/>
            </form>

            <table>
                <thead>
                    <th>TODO</th>
                    <th>Delete</th>
                </thead>
                    <TableRows tasks={this.state.tasks} removeTask={this.removeTask}/>
            </table>
        </Fragment>
        );
    }
}

class TableRows extends Component {
    getRows = () => {
        const tasks = this.props.tasks;
        const rows = tasks.map((task) => {
            return (
                <tr key={task.id}>
                    <td>{task.value}</td>
                    <td><Button variant='danger' onClick={() => this.props.removeTask(task.id)}>Delete</Button></td>
                </tr>
            );
            
        });

        return rows;
    }
    render(){
        return (
            <tbody>
                {this.getRows()}
            </tbody>
        );
    }
}

export default TodoList