import React, {Fragment, Component} from 'react'
import {Button} from 'react-bootstrap'

class TodoList extends Component {
    constructor(props){
        super(props);
        this.state = {value:'', tasks:this.props.data}
    }

    handleChange = (event) => {
        this.setState({value:event.target.value});
    }

    addTask = (event) => {
        const value = this.state.value;
        const id = this.state.currentId;
        this.setState({value:'', tasks:[...this.state.tasks, {value:value, id:id}]})
        event.preventDefault();
    }

    render() {
        return (
            <Fragment>
                <form>
                    <label>
                        {"Tâche : "}
                        <input type="text" value={this.state.value} name="task" onChange={this.handleChange}/>
                    </label>
                    <input type="submit" value="Ajouter" onClick={this.addTask}/>
                </form>

                <table>
                    <thead>
                        <th>TODO</th>
                        <th>Delete</th>
                    </thead>
                        <TableRows tasks={this.state.tasks}/>
                </table>
            </Fragment>
        );
    }
}

class TableRows extends Component {
    getRows = () => {
        const tasks = this.props.tasks;
        const rows = tasks.map((value) => {
            return (
                <tr key={tasks.indexOf(value)}>
                    <td>{value.value}</td>
                    <td></td>
                </tr>
            );
        });
        return rows;
    }
    render(){
        return (
            <tbody>
                {this.getRows()}
            </tbody>
        );
    }
}

export default TodoList