import React, { Component, Fragment } from 'react'
import { Row, Col } from 'react-bootstrap'
import Jordan from './jordan-walke.jpg'
import Logo from './logo.svg'

class Page1 extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Jordan Walke</h2>
                        <img src={Jordan} className="image shadowed"/>
                            <ul className="left">
                                <li>Ingénieur chez Facebook</li>
                                <li>Travaillait avec beaucoup de manipulations du DOM</li>
                                <li>Voulait créer un remplacement pour le stack PHP de Facebook</li>
                            </ul>
                    </Col>
                    <Col>
                        <h2>React</h2>
                        <img src={Logo} className="image"/>
                        <ul className="left">
                            <li>Librairie JavaScript pour créer des interfaces utilisateur</li>
                            <li>Structure orientée composantes</li>
                            <li>Client-Side Rendering</li>
                            <li>Déployé pour la première fois en 2011 (Fil d'actualité Facebook)</li>
                            <li>Open Source depuis 2013</li>
                        </ul>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}

export default Page1;