import React, {Fragment, Component} from 'react'
import {Row, Col, Button} from 'react-bootstrap'
import NodeLogo from './Node-logo.jpg'
import NPMLogo from './npm-logo.png'

class Page2 extends Component {
    render(){
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Node.js</h2>
                        <img src={NodeLogo} className="image shadowed"/>
                        <ul className="left">
                            <li>Environnement JavaScript séparé du DOM</li>
                            <li>React y garde une copie du DOM virtualisé</li>
                            <li>Permet l'optimisation des modification d'affichage par différentiel</li>
                        </ul>
                        <a href="https://nodejs.org/en/"><Button variant="primary">Site Web</Button></a>
                    </Col>
                    <Col>
                        <h2>NPM</h2>
                        <img src={NPMLogo} className="image"/>

                        <ul className="left">
                            <li>Node.js Package Manager</li>
                            <li>Permet d'installer React facilement</li>
                            <li><code>{"npx create-react-app <nom>"}</code></li>
                        </ul>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page2;