import React, {Component} from 'react'

class TodoList extends Component {
    render() {
        return (
            <table>
                <thead>
                    <th>TODO</th>
                    <th>Delete</th>
                </thead>
                    <TableRows tasks = {this.props.data} />
            </table>
        );
    }
}

class TableRows extends Component {
    getRows = () => {
        const tasks = this.props.tasks;
        const rows = tasks.map((value) => {
            return (
                <tr key={tasks.indexOf(value)}>
                    <td>{value.value}</td>
                    <td></td>
                </tr>
            );
        });
        return rows;
    }
    render(){
        return (
            <tbody>
                {this.getRows()}
            </tbody>
        );
    }
}

export default TodoList;