import React, {Fragment, Component} from 'react'
import {Col, Row, Button} from 'react-bootstrap'
import TodoList from './Component1.js'

class Page4 extends Component {
    constructor(props){
        super(props);
        this.state = {
            shown:false
        }
    }

    showHide = () => {
        const {shown} = this.state;
        this.setState({shown:!shown});
    }



    render(){
        let element;
        if(this.state.shown) element = <TodoList/>;
        else element = "";

        return (
            <Fragment>
                <Row>
                    <Col>
                <h2>Composantes</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        
                        <ul className="left">
                            <li>Unité de base de l'interface</li>
                            <li>Classe JavaScript (depuis ES6) ou fonction</li>
                            <li>Possède des méthodes de cycle de vie</li>
                            <li>Autonome, logique encapsulée</li>
                        </ul>
                    </Col>
                    <Col>
                        <p>Affichées comme des balises HTML :</p>
                        <code>{"<TodoList/>"}</code>
                    </Col>
                </Row>
                <Row>
                    <Col>
                    <div class="left" style={{backgroundColor:'#20242E', boxShadow:'-2px -2px 2px #17212B inset', paddingLeft:'3px'}}>
                        <code style={{marginTop:10}} className="left">
                            {"class TodoList extends Component {"}<br/>
                            <span style={{marginLeft:40}}>{"render() {"}</span><br/>
                            <span style={{marginLeft:80}}>{"return ("}</span><br/>
                            <span style={{marginLeft:120}}>{"<table>"}</span><br/>
                            <span style={{marginLeft:160}}>{"<thead>"}</span><br/>
                            <span style={{marginLeft:200}}>{"<th>TODO</th>"}</span><br/>
                            <span style={{marginLeft:200}}>{"<th>Delete</th>"}</span><br/>
                            <span style={{marginLeft:160}}>{"</thead>"}</span><br/>
                            <span style={{marginLeft:120}}>{"</table>"}</span><br/>
            <span style={{marginLeft:80}}>{");"}</span><br/>

                            <span style={{marginLeft:40}}>{"}"}</span><br/>
                            {"}"}<br/><br/>
                            {"export default TodoList;"}
                        </code>
                        </div>
                        <Button variant="success" onClick={this.showHide}>{this.state.shown ? "Cacher" : "Afficher"}</Button>

                    </Col>
                    <Col>
                            {element}
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page4