import React, {Fragment, Component} from 'react'
import {Col, Row} from 'react-bootstrap'
import TodoList from './Component3.js'
import {Button} from 'react-bootstrap'

class Page6 extends Component {
    constructor(props){
        super(props);
        this.state = {
            shown:false
        }
    }

    showHide = () => {
        const {shown} = this.state;
        this.setState({shown:!shown});
    }

    render() {
        let element;
        if(this.state.shown) element = <TodoList data={[{value:'blah', id:0}, {value:'blip', id:1}, {value:'bloup', id:2}]}/>;
        else element = "";

        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>États</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div class="left">
                            <ul>
                                <li>Variables mutables appartenant à la composante</li>
                                <li>Modifie automatiquement l'affichage lorsque modifié</li>
                            </ul>
                            
                        </div>
                    </Col>
                    <Col>
                        <p>Peut être accédé et modifié par les commandes suivantes :</p>
                        <code>{"this.state.<état>"}</code><br/>
                        <code>{"this.setState({état1:'value', état2:'value'})"}</code>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div class="left" style={{backgroundColor:'#20242E', boxShadow:'-2px -2px 2px #17212B inset', paddingLeft:'3px'}}>
                            <code>
                                {"constructor(props){"}<br/>
                                <span style={{marginLeft:40}}>{"super(props);"}</span><br/>
                                <span style={{marginLeft:40}}>{"this.state = {value:'', tasks:this.props.data}"}</span><br/>
                                {"}"}<br/><br/>
                                {"handleChange = (event) => {"}<br/>
                                <span style={{marginLeft:40}}>{"this.setState({value:event.target.value});"}</span><br/>
                                {"}"}<br/><br/>
                                {"addTask = (event) => {"}<br/>
                                <span style={{marginLeft:40}}>{"const value = this.state.value;"}</span><br/>
                                <span style={{marginLeft:40}}>{"this.setState({value:'', tasks:[...this.state.tasks, {value:value}]})"}</span><br/>
                                <span style={{marginLeft:40}}>{"event.preventDefault();"}</span><br/>
                                {"}"}
                            </code>
                        </div>

                        <Button variant="success" onClick={this.showHide}>{this.state.shown ? "Cacher" : "Afficher"}</Button>
                    </Col>
                    <Col>
                            {element}
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page6;