import React, {Fragment, Component} from 'react'
import {Row, Col} from 'react-bootstrap'

class Page12 extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>React.js</h2>
                        <p>Devrait continuer de croître dans les prochaines années et prendre une bonne partie du marché de JQuery.</p>
                        <ul className="left">
                            <li>Apporte une nouvelle approche au développement web.</li>
                            <li>Expérience de développement simple</li>
                            <li>Incentive monétaire présente</li>
                        </ul>
                    </Col>
                    <Col>
                        <h2>React Native</h2>
                        <p>Plus difficile à prédire, pourrait aller dans les deux sens :</p>
                        <ul className="left">
                            <li>Offre une rapidité de développement inégalée pour la création d'applications cross-platform</li>
                            <li>Permet une abstraction plus importante par rapport au système d'exploitation</li>
                            <li>Par contre, développement d'applications hybride apporte son lot de problèmes</li>
                        </ul>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page12