import React, {Component, Fragment} from 'react'
import {Col, Row, Button} from 'react-bootstrap'
import TodoList from './Component2.js'

class Page5 extends Component {

    constructor(props){
        super(props);
        this.state = {
            shown:false
        }
    }

    showHide = () => {
        const {shown} = this.state;
        this.setState({shown:!shown});
    }

    render() {
        let element;
        if(this.state.shown) element = <TodoList data={[{value:'blah'}, {value:'blip'}, {value:'bloup'}]}/>;
        else element = "";

        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Propriétés</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        
                        <div class="left">
                            <p>Peuvent être :</p>
                            <ul>
                                <li>Valeurs constantes</li>
                                <li>États initiaux</li>
                                <li>Fonctions du parent</li>
                            </ul>
                        </div>
                    </Col>
                    <Col>
                        <div class="left">
                            <p>Sont utilisées comme des propriétés HTML :</p>
                            <code>{"<TodoList data={[{value:'blah'}, {value:'blip'}, {value:'bloup'}]}/>"}</code>
                        </div>

                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div class="left" style={{backgroundColor:'#20242E', boxShadow:'-2px -2px 2px #17212B inset', paddingLeft:'3px'}}>
                        <code>
                            {"class TableRows extends Component {"}<br/>
                            <span style={{marginLeft:40}}>{"    getRows = () => {"}</span><br/>
                            <span style={{marginLeft:80}}>{"        const tasks = this.props.tasks;"}</span><br/>
                            <span style={{marginLeft:80}}>{"        const rows = tasks.map((value) => {"}</span><br/>
                            <span style={{marginLeft:120}}>{"            return ("}</span><br/>
                            <span style={{marginLeft:160}}>{"                <tr key={tasks.indexOf(value)}>"}</span><br/>
                            <span style={{marginLeft:200}}>{"                    <td>{value.value}</td>"}</span><br/>
                            <span style={{marginLeft:200}}>{"                    <td></td>"}</span><br/>
                            <span style={{marginLeft:160}}>{"                </tr>"}</span><br/>
                            <span style={{marginLeft:120}}>{"            );"}</span><br/>
                            <span style={{marginLeft:80}}>{"        });"}</span><br/>
                            <span style={{marginLeft:80}}>{"        return rows;"}</span><br/>
                            <span style={{marginLeft:40}}>{"    }"}</span><br/>
                            <span style={{marginLeft:40}}>{"    render(){"}</span><br/>
                            <span style={{marginLeft:80}}>{"        return ("}</span><br/>
                            <span style={{marginLeft:120}}>{"            <tbody>"}</span><br/>
                            <span style={{marginLeft:160}}>{"                {this.getRows()}"}</span><br/>
                            <span style={{marginLeft:120}}>{"            </tbody>"}</span><br/>
                            <span style={{marginLeft:80}}>{"        );"}</span><br/>
                            <span style={{marginLeft:40}}>{"    }"}</span><br/>
                            {"}"}
                        </code>

                        </div>

                        <Button variant="success" onClick={this.showHide}>{this.state.shown ? "Cacher" : "Afficher"}</Button>
                    </Col>
                    <Col>
                            {element}
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page5;