import React, {Fragment, Component} from 'react'
import {Row, Col} from 'react-bootstrap'
import JQuery from './jquery.png'

class Page11 extends Component {
    render(){
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Principal concurrent de JQuery</h2>
                        <p>La demande pour des interactions riches avec l'utilisateur augmente constamment. React offre de meilleurs outils pour répondre à la demande :</p>
                        <Row>
                            <Col>
                                <ul class="left">
                                    <li>Structure par composantes</li>
                                    <li>Encapsulation</li>
                                    <li>Virtualisation du DOM</li>
                                    <li>Scripts en premier, puis affichage</li>
                                    <li>Optimisation des mises-à-jour de l'affichage</li>
                                    <li>Syntaxe déclarative</li>
                                </ul>
                            </Col>
                            <Col>
                                <img src={JQuery} className="image"/>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page11