import React, {Fragment, Component} from 'react'
import {Col, Row} from 'react-bootstrap'
import TodoList from './Component4.js'
import {Button} from 'react-bootstrap'

class Page7 extends Component {
    constructor(props){
        super(props);
        this.state = {
            shown:false
        }
    }

    showHide = () => {
        const {shown} = this.state;
        this.setState({shown:!shown});
    }

    render() {
        let element;
        if (this.state.shown) element = <TodoList data={[{value:'blah', id:0}, {value:'blip', id:1}, {value:'bloup', id:2}]}/>;
        else element = "";
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Événements</h2>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div class="left">
                            <ul>
                                <li>Peuvent être passés à l'enfant comme propriété</li>
                                <li>Modifie automatiquement l'affichage lorsque modifié</li>
                            </ul>

                        </div>
                    </Col>
                    <Col>
                        <p>Peut être accédé et modifié par les commandes suivantes :</p>
                        <code>{"this.state.<état>"}</code><br/>
                        <code>{"this.setState({état1:'value', état2:'value'})"}</code>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div class="left" style={{backgroundColor:'#20242E', boxShadow:'-2px -2px 2px #17212B inset', paddingLeft:'3px'}}>
                            <code>
                                {"removeTask = (id) => {"}<br/>
                                <span style={{marginLeft:40}}>{"    const tasks = this.state.tasks"}</span><br/>
                                <span style={{marginLeft:40}}>{"    const filteredTasks = tasks.filter((task) => {return task.id !== id})"}</span><br/>
                                <span style={{marginLeft:40}}>{"    this.setState({tasks:filteredTasks})"}</span><br/>
                                {"}"}<br/><br/>
                                {"getRows = () => {"}<br/>
                                <span style={{marginLeft:40}}>{"    const tasks = this.props.tasks;"}</span><br/>
                                <span style={{marginLeft:40}}>{"    const rows = tasks.map((task) => {"}</span><br/>
                                <span style={{marginLeft:80}}>{"        return ("}</span><br/>
                                <span style={{marginLeft:120}}>{"            <tr key={task.id}>"}</span><br/>
                                <span style={{marginLeft:160}}>{"                <td>{task.value}</td>"}</span><br/>
                                <span style={{marginLeft:160}}>{"                <td><Button variant='danger' onClick={() => this.props.removeTask(task.id)}>Delete</Button></td>"}</span><br/>
                                <span style={{marginLeft:120}}>{"            </tr>"}</span><br/>
                                <span style={{marginLeft:80}}>{"        );"}</span><br/>
                                <span style={{marginLeft:80}}>{"        "}</span><br/>
                                <span style={{marginLeft:40}}>{"    });"}</span><br/>
                                <span style={{marginLeft:40}}>{"    return rows;"}</span><br/>
                                {"}"}
                            </code>
                        </div>
                        <Button variant="success" onClick={this.showHide}>{this.state.shown ? "Cacher" : "Afficher"}</Button>
                    </Col>
                    <Col>
                            {element}
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page7