import React, {Fragment, Component} from 'react'
import {Col, Row} from 'react-bootstrap'
import Babel from './babel-logo.png'

class Page3 extends Component {
    render(){
        return (
            <Fragment>
                <Row>
                <Col>
                    <h2>Syntaxe (JSX)</h2>
                    <ul className="left">
                        <li>JavaScript and XML</li>
                        <li>Permet la déclaration d'objet DOM dans un fichier JavaScript</li>
                        <li>Simplifie grandement la syntaxe</li>
                        <li>Compilé en JavaScript par le compilateur Babel</li>
                        <li><code>{"const element = <h1>Hello World!</h1>;"}</code></li>
                        <li><code>{"const element = React.createElement('h1', null, 'Hello World!');"}</code></li>
                    </ul>
                    <img src={Babel} className="image"/>

                </Col>
                </Row>

            </Fragment>
        );
    }
}

export default Page3