import React, { Component, Fragment } from 'react';
import './App.css';
import { Button, Container, Row, Col, } from 'react-bootstrap'
import Page1 from './Page1.js'
import Page2 from './Page2.js'
import Page3 from './Page3.js'
import Page4 from './Page4.js'
import Page5 from './Page5.js'
import Page6 from './Page6.js'
import Page7 from './Page7.js'
import Page8 from './Page8.js'
import Page9 from './Page9.js'
import Page10 from './Page10.js'
import Page11 from './Page11.js'
import Page12 from './Page12.js'
import AppNav from './AppNav.js'


//Component principale, 
class App extends Component {

  state = {
    index: 0
  }

  previous = (size) => {
    const { index } = this.state;
    if (index === 0) this.setState({ index: size - 1 });
    else this.setState({ index: index - 1 });
  }

  next = (size) => {
    const { index } = this.state;
    if (index === size - 1) this.setState({ index: 0 });
    else this.setState({ index: index + 1 });
  }

  navigate = (id) => {
    this.setState({ index: id });
  }

  getComponents = () => {
    return [
      {
        id: 0,
        name: "React.js",
        code: (
          <Page1 />
        )
      },
      {
        id:1,
        name:"Technologie Émergente (1/2)",
        code:<Page10/>
      },
      {
        id:2,
        name:"Technologie Émergente (2/2)",
        code:<Page11/>
      },
      {
        id: 3,
        name: "Dépendances",
        code: (
          <Page2 />
        )
      },
      {
        id: 4,
        name: "Points Saillants (1/5)",
        code: (
          <Page3/>
        )
      },
      {
        id: 5,
        name: "Points Saillants (2/5)",
        code: (
          <Page4/>
        )
      },
      {
        id: 6,
        name: "Points Saillants (3/5)",
        code: (
          <Page5/>
        )
      },
      {
        id: 7,
        name: "Points Saillants (4/5)",
        code: (
          <Page6/>
        )
      },
      {
        id: 8,
        name: "Points Saillants (5/5)",
        code: (
          <Page7/>
        )
      },
      {
        id: 9,
        name:"Avantages",
        code: (
          <Page8 />
        )
      },
      {
        id:10,
        name:"Désavantages",
        code: (
          <Page9 />
        )
      },
      {
        id:11,
        name:"Prédictions",
        code:<Page12/>
      }
    ]
  }

  render() {
    return (
      <Container className="App">
        <AppNav components={this.getComponents()} navigate={this.navigate} />
        <h1>{this.getComponents()[this.state.index].name}</h1>
        <Row>
          <Col>
            <Button variant="secondary" onClick={() => this.previous(this.getComponents().length)}>&larr;</Button>
            <Button variant="secondary" onClick={() => this.next(this.getComponents().length)}>&rarr;</Button>
          </Col>
        </Row>
        <Row style={{marginTop:10}}>
          <Col>
            {this.getComponents()[this.state.index].code}
          </Col>
        </Row>
        
      </Container >
    );
  }
}

export default App;
