import React, {Fragment, Component} from 'react'
import {Row, Col} from 'react-bootstrap'
import Stats from './stats.png'
import Datanyze from './datanyze-logo.png'
import Native from './native.png'

class Page10 extends Component {
    render() {
        return (
            <Fragment>
                <Row>
                    <Col>
                        <h2>Mode React</h2>
                        <p>Selon le site d'analyse technologique Datanyze, React est :</p>
                        <ul className="left">
                            <li>Utilisé par 0.056% de l'entièreté des sites web</li>
                            <li>Utilisé par 19 des 100 sites les plus importants (Alexa Top 100)</li>
                            <li>La 4<sup>e</sup> technologie web la plus utilisée globalement</li>
                        </ul>
                        <img src={Stats} className="image shadowed"/>
                        <img src={Datanyze} className="image" style={{marginTop:10}}/>

                        <p style={{marginTop:10}}>C'est la technologie web la plus payante à connaître pour les développeurs Full Stack selon un sondage par StackOverflow (105 000$/année)</p>
                        <p>React est notamment utilisé sur les sites de <a href="https://www.facebook.com/">Facebook</a>, <a href="https://www.instagram.com/">Instagram</a>, <a href="https://soundcloud.com/">SoundCloud</a>, <a href="https://www.tesla.com/">Tesla</a>, <a href="https://www.netflix.com/">Netflix</a>, <a href="https://www.skype.com/en/">Skype</a>, <a href="https://www.discord.gg/">Discord</a>...</p>
                    </Col>
                    <Col>
                        <h2>React Native</h2>

                        <p>Framework pour interfaces graphiques cross-platform en JavaScript utilisant la structure de React. Code once, compile anywhere.</p>
                        <p> Permet de créer des applications pour :</p>
                        <ul className="left">
                            <li>Android</li>
                            <li>iOS</li>
                            <li>Windows</li>
                        </ul>
                        <img src={Native} className="image shadowed"/>
                        
                        <p>Utilisé pour les applications mobiles de tous les sites web mentionnés précédemment.</p>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default Page10